
.nav_pannel {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  height: 100%;
  width: 170px;
}
.nav_pannel .logo {
    text-align: center;
    background-color: #3EBE8B;
    height: 30px;
    padding: 12px 0;
}
.nav_pannel .logo > img {
      height: 30px;
}
.nav_pannel .title {
    position: relative;
    z-index: 29;
    background-color: #34495E;
    color: #fff;
    font-weight: bold;
    font-size: 15px;
    padding: 20px;
    box-shadow: -5px 1px 10px rgba(0, 0, 0, 0.2);
}
.nav_pannel .title span {
      position: relative;
      top: 1px;
      margin-right: 3px;
}
.nav_pannel .first_nav_list {
    overflow: hidden;
    width: 170px;
}
.nav_pannel .first_nav_list ul {
      position: relative;
      z-index: 19;
      width: 178px;
      background-color: #34495E;
      height: calc(100vh - 140px);
      overflow-y: scroll;
      padding-bottom: 50px;
}
.nav_pannel .first_nav_list .first_nav_item {
      position: relative;
      border-bottom: 1px solid #2d4256;
      transition: all .2s linear;
      cursor: pointer;
}
.nav_pannel .first_nav_list .first_nav_item span {
        display: block;
        font-size: 12px;
        line-height: 20px;
        color: #96a0ab;
        padding: 12px 20px;
        transition: all .1s linear;
}
.nav_pannel .first_nav_list .first_nav_item:hover {
      background-color: #3c5065;
}
.nav_pannel .first_nav_list .active {
      background-color: #4d6277;
}
.nav_pannel .first_nav_list .active span {
        font-weight: bold;
        color: #fff;
        border-left: 4px solid #3EBE8C;
}
.nav_pannel .first_nav_list .hidden_block {
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 29;
      width: 100%;
      height: 40px;
      background: linear-gradient(transparent, #34495E);
}
.nav_pannel .second_nav_list {
    position: absolute;
    top: 54px;
    left: -50px;
    z-index: 9;
    width: 200px;
    overflow-x: hidden;
    background-color: rgba(255, 255, 255, 0.92);
    border-right: 1px solid #EBEEF3;
    opacity: 0;
    transition: all .2s ease-out;
}
.nav_pannel .second_nav_list > ul {
      width: 208px;
      overflow-y: scroll;
      height: calc(100vh - 104px);
      padding-bottom: 50px;
}
.nav_pannel .second_nav_list .second_nav_item {
      cursor: pointer;
      border-bottom: 1px solid #f1f3f7;
      transition: all .2s linear;
}
.nav_pannel .second_nav_list .second_nav_item span {
        display: block;
        color: #34495E;
        padding: 12px 20px;
        font-size: 12px;
        line-height: 20px;
        transition: all .1s linear;
}
.nav_pannel .second_nav_list .second_nav_item:hover {
      background-color: #f1f3f7;
}
.nav_pannel .second_nav_list .active {
      background-color: #EBEEF3;
}
.nav_pannel .second_nav_list .active span {
        border-right: 4px solid #3ebe8b;
        font-weight: bold;
}
.nav_pannel .second_nav_list .hidden_block {
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 29;
      width: 100%;
      height: 40px;
      background: linear-gradient(transparent, #fff);
}
.nav_pannel .second_nav_active {
    opacity: 1;
    left: 170px;
}
.nav_pannel .second_nav_selected {
    left: 0;
}
.nav_pannel:hover .second_nav_selected {
  left: 170px;
}



