
.header_pannel > .header {
  position: relative;
  position: fixed;
  background: linear-gradient(to right, #41DD9B, #3BB3B7);
  top: 0;
  left: 170px;
  right: 0;
  z-index: 999;
  height: 54px;
  font-size: 14px;
  line-height: 40px;
  min-width: 1030px;
  padding: 0 30px;
}
.header_pannel > .header .search {
    line-height: 54px;
    color: #fff;
    font-size: 13px;
}
.header_pannel > .header .search > span {
      position: relative;
      top: 1px;
      cursor: pointer;
}
.header_pannel > .header .search > input {
      color: #fff;
      background-color: transparent;
      margin-right: 10px;
}
.header_pannel > .header .search .search_fields {
      position: relative;
      display: inline-block;
      line-height: 20px;
      border: 1px solid #fff;
      border-radius: 16px;
      padding: 4px 15px;
      cursor: pointer;
      transition: all .2s linear;
}
.header_pannel > .header .search .search_fields span {
        position: relative;
        top: 1px;
        left: -4px;
        display: inline-block;
        -webkit-transform: rotate(270deg) scale(0.5);
                transform: rotate(270deg) scale(0.5);
}
.header_pannel > .header .search .search_fields:hover {
      background-color: rgba(0, 0, 0, 0.05);
}
.header_pannel > .header .search .search_fields_list {
      position: absolute;
      top: 47px;
      left: 215px;
      background-color: #fff;
      padding: 10px 0;
      border-radius: 3px;
      color: #34495E;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
.header_pannel > .header .search .search_fields_list .search_fields_list_item {
        width: calc(100% - 40px);
        padding: 15px 20px;
        line-height: 13px;
        cursor: pointer;
        transition: all .2s linear;
}
.header_pannel > .header .search .search_fields_list .search_fields_list_item:hover {
        background-color: #edf3f6;
}
.header_pannel > .header .search .search_fields_list .search_fields_at {
        font-size: 12px;
        color: #8CA0B3;
        line-height: 12px;
        padding: 10px 20px;
        cursor: pointer;
}
.header_pannel > .header .username {
    font-size: 13px;
    line-height: 54px;
    color: #fff;
    width: 120px;
    text-align: center;
    border-left: 1px solid #68d8d5;
}
.header_pannel > .header .logout {
    position: relative;
    height: 54px;
    width: 100px;
    text-align: center;
    line-height: 54px;
    cursor: pointer;
    color: #fff;
    border-left: 1px solid #68d8d5;
}
.header_pannel > .header .logout .icon-switch {
      position: relative;
      top: 3px;
      display: inline-block;
      font-size: 16px;
      opacity: 0;
      -webkit-transform: scale(0.1);
              transform: scale(0.1);
      transition: all .2s ease-out;
}
.header_pannel > .header .logout .text {
      position: absolute;
      top: 0px;
      left: 23px;
      font-size: 13px;
      transition: all .2s linear;
}
.header_pannel > .header .logout:hover .icon-switch {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}
.header_pannel > .header .logout:hover .text {
    opacity: 0;
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
}



