
.boxy {
  transition: all .2s ease-in-out;
}
.boxy .boxy_content {
    position: absolute;
    top: 45%;
    left: 50%;
    z-index: 99999;
    width: 270px;
    margin-top: -100px;
    margin-left: -165px;
    background-color: #fff;
    border-radius: 3px;
    padding: 70px 30px;
    text-align: center;
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.05);
    transition: all .2s ease-in-out;
}
.boxy .boxy_content .message {
      display: inline-block;
      font-size: 16px;
      color: #333;
      line-height: 30px;
}
.boxy .boxy_content .password {
      width: 50px;
}
.boxy .boxy_content .confirm_option {
      margin-top: 25px;
      margin-bottom: 35px;
}
.boxy .boxy_content .confirm_option .confirm {
        background-color: #2dc38c;
        margin-left: 25px;
}
.boxy .boxy_content .confirm_option .cancel {
        color: #2dc38c;
        border-color: #2dc38c;
}
.boxy .boxy_content .warning_option {
      margin-top: 25px;
      margin-bottom: 35px;
}
.boxy .boxy_content .warning_option .warning {
        background-color: #FB7648;
        margin-left: 25px;
}
.boxy .boxy_content .warning_option .cancel {
        color: #FB7648;
        border-color: #FB7648;
}
.boxy .boxy_content .prompt_option {
      margin-top: 25px;
      margin-bottom: 35px;
}
.boxy .boxy_content .prompt_option .prompt {
        background-color: #2dc38c;
}
.boxy .confirm_boxy {
    padding-top: 60px;
    padding-bottom: 0;
}
.showBoxyMash {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.2);
}
.showBoxyMash .showBoxy {
    display: block;
    transition: all .2s ease-in-out;
}



