
.book_details {
  margin: 30px 0;
  margin-left: 60px;
  width: calc(100vw - 270px);
}
.book_details .details_info {
    margin-bottom: 10px;
}
.book_details .details_info .title {
      font-size: 18px;
      font-weight: bold;
      color: #6C7C8B;
}
.book_details > .main_page {
    position: relative;
    overflow: visible;
    background-color: #fff;
    margin-bottom: 40px;
}
.book_details > .main_page .banner {
      position: relative;
      height: 240px;
      overflow: hidden;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      text-align: center;
}
.book_details > .main_page .banner .back {
        position: absolute;
        top: 20px;
        left: 30px;
        color: #fff;
        font-size: 15px;
        cursor: pointer;
        opacity: 0.85;
}
.book_details > .main_page .banner .back > span {
          font-size: 12px;
          transition: all .2s ease-out;
}
.book_details > .main_page .banner .back:hover {
        opacity: 1;
}
.book_details > .main_page .banner .back:hover > span {
          margin-left: -2px;
          margin-right: 2px;
}
.book_details > .main_page .banner .title {
        position: absolute;
        top: 50px;
        left: 0;
        height: 60px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        width: calc(100% - 60px);
        padding: 0 30px;
        color: #fff;
        font-weight: bold;
        font-size: 24px;
        line-height: 32px;
        text-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
}
.book_details > .main_page .banner .authors {
        position: absolute;
        bottom: 105px;
        width: 100%;
        text-align: center;
        font-size: 13px;
        color: #fff;
        font-weight: normal;
        line-height: 12px;
        text-shadow: 0 0 1px rgba(0, 0, 0, 0.3);
}
.book_details > .main_page .banner img {
        width: 100%;
}
.book_details > .main_page .cover {
      position: absolute;
      top: 160px;
      left: 50%;
      margin-left: -50px;
      width: 100px;
      height: 100px;
      overflow: hidden;
      border: 1px solid #EBEEF3;
      border-radius: 3px;
      background-color: #F4F6FA;
}
.book_details > .main_page .cover span {
        position: absolute;
        top: 50px;
        left: 50px;
        margin-left: -7px;
        margin-top: -7px;
        z-index: 1;
        color: #8CA0B3;
        font-size: 14px;
}
.book_details > .main_page .cover img {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 9;
        width: 100%;
}
.book_details > .main_page .download {
      width: 100%;
      text-align: center;
      margin-top: 60px;
}
.book_details > .main_page .download .download_now {
        background-color: #3EBE8C;
        margin-right: 20px;
        width: 120px;
}
.book_details > .main_page .download .to_download {
        color: #4DA1FF;
        border-color: #4DA1FF;
}
.book_details > .main_page .details {
      width: 100%;
}
.book_details > .main_page .details .line {
        display: inline-block;
        width: 100%;
        margin-top: 40px;
        border-bottom: 1px solid #EBEEF3;
}
.book_details > .main_page .details .details_info {
        text-align: center;
        padding-top: 30px;
        padding-bottom: 50px;
        padding-left: 13vw;
}
.book_details > .main_page .details .details_info > li {
          display: inline-block;
          text-align: left;
          width: 60%;
          font-size: 14px;
          padding: 15px 0;
}
.book_details > .main_page .details .details_info > li .key_icon {
            position: relative;
            top: 1px;
            display: inline-block;
            color: #34495E;
            margin-right: 10px;
}
.book_details > .main_page .details .details_info > li .key {
            display: inline-block;
            color: #34495E;
            margin-bottom: 5px;
            width: 140px;
}
.book_details > .main_page .details .details_info > li .value {
            color: #43B679;
}



