.fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.fade-slow-enter-active, .fade-slow-leave-active {
  transition: opacity 2s;
}
.fade-slow-enter, .fade-slow-leave-to {
  opacity: 0;
}
.fade-quick-enter-active, .fade-quick-leave-active {
  transition: opacity .1s;
}
.fade-quick-enter, .fade-quick-leave-to {
  opacity: 0;
}
.slide-from-top-enter-active {
  transition: all .3s ease-out;
}
.slide-from-top-leave-active {
  transition: all .8s ease-out;
}
.slide-from-top-enter, .slide-from-top-leave-to {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  opacity: 0;
}
.tip > p {
  text-align: center;
  font-size: 18px;
  color: #474F58;
}
.tip > p > .tip_head {
    position: relative;
    top: -10px;
    left: -10px;
    color: #9BA3AF;
}
.visibility {
  opacity: 1 !important;
}
.pull_left {
  float: left !important;
}
.pull_right {
  float: right !important;
}
.bold {
  font-weight: bold !important;
}
.text_center {
  text-align: center !important;
}
.no_warp {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.btn_solid, .btn_solid_small, .btn_solid_large {
  cursor: pointer;
  display: inline-block;
  border-radius: 3px;
  text-align: center;
  font-size: 14px;
  color: #fff;
  background-color: #ccc;
  transition: all .1s linear;
}
.btn_solid {
  padding: 0 20px;
  height: 38px;
  line-height: 38px;
}
.btn_solid_small {
  font-size: 14px;
  padding: 0 12px;
  height: 30px;
  line-height: 30px;
}
.btn_solid_large {
  font-size: 18px;
  height: 48px;
  line-height: 48px;
  border-radius: 4px;
  padding: 0 40px;
}
.btn_solid:hover, .btn_solid_large:hover, .btn_solid_small:hover {
  opacity: 0.9;
}
.btn_solid_disabled {
  background-color: #ccc !important;
  cursor: default !important;
}
.btn_hollow, .btn_hollow_small, .btn_hollow_large {
  cursor: pointer;
  display: inline-block;
  color: #ccc;
  border: 1px solid #ccc;
  border-radius: 3px;
  text-align: center;
  transition: all .1s linear;
}
.btn_hollow {
  padding: 0 19px;
  height: 36px;
  line-height: 36px;
}
.btn_hollow_small {
  font-size: 14px;
  padding: 0 11px;
  height: 28px;
  line-height: 28px;
}
.btn_hollow_large {
  height: 46px;
  line-height: 46px;
  border-radius: 4px;
  padding: 0 40px;
}
.btn_hollow_large:hover, .btn_hollow:hover, .btn_hollow_small:hover {
  background-color: rgba(0, 0, 0, 0.02);
}
.btn_hollow_disabled {
  color: #ccc !important;
  border-color: #ccc !important;
  cursor: default !important;
}
.rotate {
  position: absolute;
  -webkit-animation: rotate 1.5s linear infinite;
          animation: rotate 1.5s linear infinite;
}
@-webkit-keyframes rotate {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rotate {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.blur {
  -webkit-filter: blur(15px);
          filter: blur(15px);
  filter: progid:DXImageTransform.Microsoft.Blur(PixelRadius=10, MakeShadow=false);
  /* IE6~IE9 */
}
#app {
  width: 100%;
}



